body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
.advantages {
  padding: 32px 30px 29px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .advantages {
    padding: 58px 50px 83px;
  }
}
@media (min-width: 1150px) {
  .advantages {
    padding: 68px 225px 90px;
  }
}

.advantages__title {
  margin: 0;
  font-weight: 700;
}
@media (max-width: 767px) {
  .advantages__title {
    margin-bottom: 28px;
    font-size: 25px;
    line-height: 30px;
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .advantages__title {
    margin-bottom: 41px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .advantages__title {
    margin-bottom: 39px;
    font-size: 53px;
    line-height: 57px;
  }
}

.advantages__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
@media (min-width: 500px) and (max-width: 767px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
  }
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 768px) {
  .advantages__list {
    row-gap: 55px;
  }
}

.advantages__item {
  position: relative;
  font-weight: 700;
}
.advantages__item::before {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
}
.advantages__item--flower::before {
  top: 0;
  left: 9px;
  width: 33px;
  height: 38px;
  background-image: url("../img/sprite.svg#advantage-flower");
}
.advantages__item--ball::before {
  top: 2px;
  left: 2px;
  width: 35px;
  height: 34px;
  background-image: url("../img/sprite.svg#advantage-ball");
}
.advantages__item--money::before {
  top: 0;
  left: 0;
  width: 47px;
  height: 27px;
  background-image: url("../img/sprite.svg#advantage-money");
}
.advantages__item--rocket::before {
  top: 3px;
  left: -5px;
  width: 55px;
  height: 23px;
  background-image: url("../img/sprite.svg#advantage-rocket");
}
.advantages__item--heart::before {
  top: 5px;
  left: 6px;
  width: 34px;
  height: 27px;
  background-image: url("../img/sprite.svg#advantage-heart");
}
.advantages__item--box::before {
  top: 2px;
  left: 3px;
  width: 33px;
  height: 32px;
  background-image: url("../img/sprite.svg#advantage-box");
}
@media (max-width: 767px) {
  .advantages__item {
    padding-top: 54px;
    font-size: 17px;
    line-height: 20px;
    margin-bottom: 29px;
  }
  .advantages__item:last-child {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .advantages__item {
    font-size: 20px;
    line-height: 22px;
    padding-top: 56px;
    padding-right: 20px;
  }
  .advantages__item--flower {
    order: 1;
  }
  .advantages__item--ball {
    order: 2;
  }
  .advantages__item--money {
    order: 3;
  }
  .advantages__item--rocket {
    order: 4;
  }
  .advantages__item--heart {
    order: 5;
  }
  .advantages__item--box {
    order: 6;
  }
}

.button {
  display: block;
  box-sizing: border-box;
  padding: 15px 20px;
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  font-family: inherit;
  color: #444444;
  background-color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  border: 2px solid #444444;
  cursor: pointer;
  transition: 0.5s;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
.button--primary {
  padding: 18px 67px 18px 68px;
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
  cursor: pointer;
}
.button:focus-visible {
  outline: none;
  border-color: #63d1bb;
}

.choose-form {
  display: grid;
}
@media (min-width: 768px) {
  .choose-form {
    justify-content: center;
  }
}

.choose-form__radio-buttons {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 6px;
  margin-bottom: 31px;
}
@media (min-width: 768px) {
  .choose-form__radio-buttons {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.contacts {
  padding: 38px 0 39px;
  font-size: 14px;
  line-height: 30px;
  color: #444444;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .contacts {
    padding: 68px 0 57px;
  }
}
@media (min-width: 1150px) {
  .contacts {
    display: grid;
    grid-template-columns: 1fr 1.6fr;
    grid-template-rows: repeat(2, min-content);
    column-gap: 23px;
    padding: 71px 50px 105px 224px;
  }
}

.contacts__wrapper {
  position: relative;
  padding: 0 30px 10px 29px;
  margin-bottom: 52px;
}
@media (min-width: 768px) {
  .contacts__wrapper::before {
    content: "";
    position: absolute;
    width: 47px;
    height: 81px;
    top: -22px;
    right: 52px;
    background: url("../img/sprite.svg#flag");
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .contacts__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0 50px;
    margin-bottom: 49px;
  }
}
@media (min-width: 1150px) {
  .contacts__wrapper {
    margin-bottom: 80px;
    padding: 77px 0 0;
  }
  .contacts__wrapper::before {
    top: 63px;
    left: -111px;
  }
}

.contacts__title {
  margin: 0;
  margin-bottom: 28px;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .contacts__title {
    grid-column: 1/-1;
    margin-bottom: 50px;
    padding-right: 80px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .contacts__title {
    margin-bottom: 37px;
    font-size: 53px;
    line-height: 57px;
  }
}

.contacts__wrapper-email,
.contacts__wrapper-address {
  display: grid;
  grid-template-columns: 1.3fr 3fr;
  margin: 0;
}
@media (min-width: 768px) {
  .contacts__wrapper-email,
.contacts__wrapper-address {
    grid-template-columns: 17% 83%;
    line-height: 30px;
    font-size: 17px;
    gap: 20px;
  }
}
@media (min-width: 1150px) {
  .contacts__wrapper-email,
.contacts__wrapper-address {
    grid-template-columns: 18% 82%;
  }
}

.contacts__wrapper-email {
  margin-bottom: 30px;
}

.contacts__email {
  display: inline-block;
  position: relative;
  justify-self: flex-start;
  align-self: flex-start;
  color: inherit;
  transition: 0.5s;
}
.contacts__email::after {
  content: "";
  position: absolute;
  height: 1px;
  bottom: 2px;
  left: 0;
  right: 0;
  background-color: #62d1ba;
}
@media (min-width: 1150px) {
  .contacts__email::after {
    bottom: -5px;
  }
}
.contacts__email:hover {
  color: #46c1ae;
}
.contacts__email:hover::after {
  background-color: #46c1ae;
}
.contacts__email:active {
  color: #46c1ae;
}
.contacts__email:active::after {
  background-color: #46c1ae;
  opacity: 0.1;
}
.contacts__email:focus-visible {
  outline: 2px solid #62d1ba;
}

.contacts__address {
  font-style: normal;
}

.contacts__map {
  display: grid;
  margin-bottom: 42px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .contacts__map {
    margin-bottom: 54px;
  }
}
@media (min-width: 1150px) {
  .contacts__map {
    grid-column: 2/-1;
    grid-row: 1/-1;
    margin-bottom: 0;
  }
}

.contacts__map-point {
  grid-column: 1/-1;
  grid-row: 1/-1;
  border: none;
  display: block;
  width: 100%;
  height: 454px;
  z-index: 2;
}
@media (min-width: 768px) {
  .contacts__map-point {
    height: 457px;
  }
}

.contacts__image-wrapper {
  grid-column: 1/-1;
  grid-row: 1/-1;
}

.contacts__map-image {
  display: block;
}

.contacts__button {
  margin-left: 30px;
  margin-right: 30px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .contacts__button {
    max-width: 390px;
    padding: 16px 39px 13px;
    margin-left: auto;
    margin-right: auto;
    font-size: 17px;
  }
}
@media (min-width: 1150px) {
  .contacts__button {
    padding: 14px 40px;
    align-self: flex-end;
    justify-self: flex-start;
    margin: 0;
    font-size: 17px;
  }
}

.container {
  max-width: 1150px;
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto;
}

.copyright {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .copyright {
    justify-self: end;
  }
}

.copyright__text {
  margin-right: 27px;
  font-size: 19px;
  line-height: 18px;
  font-weight: 700;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .copyright__text {
    margin-bottom: 8px;
  }
}
@media (min-width: 1150px) {
  .copyright__text {
    margin-right: 30px;
    padding-bottom: 9px;
  }
}

.copyright__link .copyright__icon {
  fill: #63d1bb;
  padding-top: 3px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .copyright__link .copyright__icon {
    padding: 0;
  }
}
.copyright__link:hover .copyright__icon {
  fill: #46c1ae;
}
.copyright__link:active .copyright__icon {
  fill: #46c1ae;
  opacity: 0.3;
}
.copyright__link:focus-visible {
  outline: 2px solid #62d1ba;
}

.features {
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .features {
    display: grid;
    grid-template-columns: 50% 50%;
  }
}

.features__link {
  position: relative;
  display: block;
  padding: 23px 110px 31px 31px;
  color: #ffffff;
  font-weight: 700;
  background-color: #63d1bb;
  line-height: 24px;
  transition: 0.3s;
  transition-duration: easy;
}
.features__link::before {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  right: 0;
  width: 100%;
  height: 5px;
  background-color: inherit;
}
@media (max-width: 767px) {
  .features__link::before {
    bottom: 0;
  }
}
.features__link::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 62px;
  top: 25%;
  right: 37px;
  background-repeat: no-repeat;
  background-image: url("../img/sprite.svg#interior");
}
.features__link:hover {
  background-color: #59d5bc;
  transform: translateY(-5px);
}
.features__link:hover::after {
  opacity: 0.7;
}
@media (max-width: 767px) {
  .features__link:hover::before {
    bottom: -5px;
  }
}
.features__link:active {
  background-color: #59d5bc;
}
.features__link:active::after {
  opacity: 0.3;
}
.features__link:focus-visible {
  outline: 2px solid #000000;
  outline-offset: -6px;
}
@media (max-width: 767px) {
  .features__link {
    font-size: 19px;
  }
}
@media (min-width: 768px) {
  .features__link {
    font-size: 20px;
    line-height: 30px;
    padding-left: 37px;
  }
}

@media (max-width: 767px) {
  .features__item:nth-child(2n) .features__link {
    background-color: #56c3b2;
  }
  .features__item:nth-child(2n) .features__link::after {
    width: 60px;
    height: 60px;
    right: 27px;
    top: 23%;
    background-image: url("../img/sprite.svg#toy");
  }
  .features__item:nth-child(2n) .features__link:hover, .features__item:nth-child(2n) .features__link:active {
    background-color: #46c1ae;
  }
}
@media (min-width: 768px) {
  .features__item:nth-child(4n-1) .features__link, .features__item:nth-child(4n-2) .features__link {
    background-color: #56c3b2;
  }
  .features__item:nth-child(4n-1) .features__link::after, .features__item:nth-child(4n-2) .features__link::after {
    width: 60px;
    height: 60px;
    right: 33px;
    top: 29%;
    mask: url("../img/sprite.svg#toy");
    background-color: #149080;
  }
  .features__item:nth-child(4n-1) .features__link:hover, .features__item:nth-child(4n-2) .features__link:hover {
    background-color: #46c1ae;
  }
}

.form {
  padding: 15px 30px 55px 27px;
}
@media (min-width: 768px) {
  .form {
    padding: 0;
  }
}

.form__group {
  padding: 0;
  border: none;
  margin-bottom: 29px;
}
@media (min-width: 768px) {
  .form__group:first-child .form__label:not(:last-child) {
    margin-bottom: 22px;
  }
}
.form__group--name {
  margin-bottom: 32px;
}
.form__group--name .form__text {
  padding-left: 0;
}
.form__group--name .form__title {
  margin-bottom: 28px;
}
.form__group:nth-child(n+3) .form__label {
  padding: 0;
  padding-left: 3px;
}
@media (max-width: 767px) {
  .form__group:nth-child(n+3) .form__label {
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .form__group:nth-child(n+3) .form__label {
    align-items: center;
    margin-bottom: 3px;
  }
}
.form__group--name .form__labels-wrapper {
  display: grid;
  gap: 21px;
}
.form__group--phone {
  margin-bottom: 34px;
}
.form__group--phone .form__title {
  margin-bottom: 0;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__group--phone .form__container {
    padding-top: 44px;
    padding-bottom: 10px;
  }
}
.form__group--mail {
  margin-bottom: 35px;
}
.form__group--mail .form__title {
  margin-bottom: 1px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__group--mail .form__container {
    padding-top: 9px;
    padding-bottom: 48px;
  }
}
.form__group--text {
  margin-bottom: 2px;
}
.form__group--text .form__title {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .form__group {
    display: grid;
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__group {
    grid-template-columns: 24.5% 75%;
    padding: 49px 50px 49px;
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .form__group {
    grid-template-columns: 13.6% 86.4%;
    padding: 61px 225px 19px 112px;
  }
  .form__group:nth-child(2) .form__container {
    display: grid;
    grid-template-columns: 112px 789px;
    padding: 48px 111px 50px;
  }
  .form__group--name {
    padding: 49px 225px 13px 112px;
  }
  .form__group--phone .form__container, .form__group--mail .form__container {
    display: grid;
    grid-template-columns: 112px 702px;
    padding: 47px 111px 0;
  }
  .form__group--mail .form__container {
    padding: 19px 111px 50px;
  }
  .form__group--text {
    padding: 48px 225px 45px 112px;
  }
}
@media (min-width: 768px) {
  .form__group--color {
    display: block;
    padding: 0;
    margin: 0;
    background-color: #f1f1f1;
  }
}

@media (max-width: 767px) {
  .form__group--color .form__container,
.form__group:nth-child(3),
.form__group:nth-child(5n+1) {
    border-top: 1px solid #62d1ba;
  }
}

.form__container {
  padding: 0;
  border: none;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__container {
    display: grid;
    grid-template-columns: 1fr 3fr;
    padding: 48px 50px 50px;
  }
}

.form__title {
  margin: 0 0 17px;
  line-height: 18px;
  font-weight: 700;
  color: #63d1bb;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .form__title {
    padding-left: 4px;
    padding-right: 21px;
  }
}
@media (min-width: 768px) {
  .form__title {
    padding: 0;
    margin-top: 18px;
    margin-bottom: 0;
    float: left;
  }
}

.form__labels-wrapper--grid {
  display: grid;
}
@media (max-width: 767px) {
  .form__labels-wrapper--grid {
    gap: 15px;
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__labels-wrapper--grid {
    grid-template-columns: 52% 48%;
  }
}
@media (min-width: 1150px) {
  .form__labels-wrapper--grid {
    grid-template-columns: repeat(3, 263px);
  }
}
@media (min-width: 768px) {
  .form__labels-wrapper--grid {
    row-gap: 22px;
  }
}

.form__label {
  position: relative;
  display: flex;
  padding-top: 15px;
  padding-bottom: 13px;
}
@media (min-width: 768px) {
  .form__label {
    padding-top: 18px;
    padding-bottom: 18px;
  }
}

.form__checker {
  position: relative;
  appearance: none;
  margin: 0;
}
.form__checker::before, .form__checker::after {
  content: "";
  position: absolute;
  box-sizing: border-box;
}
.form__checker[type=radio]::before {
  width: 42px;
  height: 42px;
  top: 50%;
  left: 2px;
  transform: translateY(-50%);
  border: 2px solid #444444;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form__checker[type=radio]::before {
    width: 52px;
    height: 52px;
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__checker[type=radio]::before {
    left: 5px;
  }
}
.form__checker[type=radio]:checked::after {
  top: 50%;
  left: 14px;
  width: 18px;
  height: 18px;
  transform: translateY(-50%);
  background-color: #62d1ba;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form__checker[type=radio]:checked::after {
    left: 20px;
    width: 23px;
    height: 23px;
  }
}
@media (min-width: 1150px) {
  .form__checker[type=radio]:checked::after {
    left: 17px;
  }
}
.form__checker[type=checkbox]::before {
  width: 38px;
  height: 38px;
  top: 50%;
  left: 2px;
  background-color: transparent;
  border: 2px solid #444444;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .form__checker[type=checkbox]::before {
    width: 52px;
    height: 52px;
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__checker[type=checkbox]::before {
    left: 0;
  }
}
.form__checker[type=checkbox]:checked::after {
  width: 38px;
  height: 38px;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../img/sprite.svg#tick-checkbox");
  background-repeat: no-repeat;
  background-size: 23px 16px;
  background-position: 0 50%;
}
@media (min-width: 768px) {
  .form__checker[type=checkbox]:checked::after {
    background-size: 32px 22px;
  }
}
.form__checker:focus {
  outline: none;
}
.form__checker:focus-visible::before {
  outline: 3px solid #62d1ba;
}
.form__checker:hover::before {
  border: 2px solid #62d1ba;
}
.form__checker:active::before {
  border: 2px solid rgba(0, 0, 0, 0.3);
}

.form__field {
  padding: 17px 8px 16px 0;
  border: none;
  box-shadow: inset 0 -2px 0 #000000;
  background-color: transparent;
  font-size: 17px;
  color: #444444;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .form__field {
    padding-left: 16px;
  }
}
.form__field::placeholder {
  font-size: 17px;
  line-height: 18px;
  opacity: 0.3;
